/*device-width*/
.main-header {
  min-height: 56px;
  display: flex;
  justify-content: flex-end;
  z-index: 1;
  position: relative;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .main-header--js-disabled {
    height: auto;
  }
}
@media (min-width: 1200px) {
  .main-header {
    max-width: 1200px;
  }
}

.main-header__logo {
  position: absolute;
  left: 50%;
  margin-left: -92.5px;
}
.main-header__logo:focus {
  opacity: 0.6;
}
.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}

.main-header__nav {
  width: 100%;
  display: none;
}
.main-header__nav--opened {
  display: block;
}
@media (max-width: 767px) {
  .main-header__nav {
    position: absolute;
    top: 0;
    left: 0;
  }
}
@media (min-width: 768px) and (max-width: 1199px) {
  .main-header__nav {
    display: block;
  }
}
@media (min-width: 1200px) {
  .main-header__nav {
    display: block;
  }
}

@media (max-width: 767px) {
  .main-header--js-disabled .main-header__nav {
    position: static;
    margin-top: 56px;
    display: block;
  }
}
.main-header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-header__toggle {
    display: block;
    border: none;
    width: 23px;
    height: 22px;
    padding: 0;
    top: 17px;
    right: 20px;
    position: absolute;
    cursor: pointer;
    background-color: transparent;
    color: #81b3d2;
    fill: transparent;
  }
  .main-header__toggle:hover use {
    fill: rgba(129, 179, 210, 0.6);
    color: transparent;
  }
  .main-header__toggle:active use {
    fill: rgba(129, 179, 210, 0.3);
    color: transparent;
  }
}

.main-header--js-disabled .main-header__toggle {
  display: none;
}

