.main-nav__close {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__close {
    display: block;
    height: 55px;
    width: 61px;
    position: absolute;
    top: 0;
    right: 0;
    border: none;
    z-index: 1;
    background-color: transparent;
    cursor: pointer;
    color: #ffffff;
    fill: transparent;
  }
  .main-nav__close:hover use {
    fill: rgba(255, 255, 255, 0.6);
    color: transparent;
  }
  .main-nav__close:active use {
    fill: rgba(255, 255, 255, 0.3);
    color: transparent;
  }
}

