.radio {
  flex-shrink: 0;
  flex-grow: 0;
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: #f2f2f2;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: -62px;
  margin: -23px 0;
}

input[type=radio]:checked + .radio::before {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #333333;
  content: "";
  display: block;
}

input[type=radio]:focus + .radio {
  box-shadow: 0 0 1px 2px #ffffff, 0 0 2px 4px #81b3d2;
}

label:hover .radio {
  background: #e5e5e5;
}

label:active .radio {
  background: #d5d5d5;
}
label:active .radio::before {
  opacity: 0.3;
}

.checkbox {
  width: 46px;
  height: 46px;
  border-radius: 4px;
  background-color: #f2f2f2;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: -62px;
  margin: -23px 0;
}

input[type=checkbox]:checked + .checkbox::before {
  content: "";
  display: block;
  background: url("../../img/tick.svg") no-repeat center;
  width: 22px;
  height: 16px;
}

input[type=checkbox]:focus + .checkbox {
  box-shadow: 0 0 1px 2px #ffffff, 0 0 2px 4px #81b3d2;
}

label:hover .checkbox {
  background: #e5e5e5;
}

label:active .checkbox {
  background: #d5d5d5;
}
label:active .checkbox::before {
  opacity: 0.3;
}